import React, { useState } from "react";
import NavBar from "./NavBar";
import LibraryListHeader from "./LibraryListHeader";
import SearchBar from "./SearchBar";
import IndividualBook from "./IndividualBook";

let books = require("../static/books");

const Student = () => {
  const [booksList, setBooksList] = useState([...books]);

  const booksListUpdate = (e) => {
    let list = [];
    list = books.filter((book) =>
      book.name.toLowerCase().includes(e.target.value.toLowerCase())
    );
    setBooksList([...list]);
  };

  return (
    <>
      <NavBar />
      <div className="row library">
        <SearchBar onChange={booksListUpdate} />
        <br />
        <div className="my-5 center">
          <LibraryListHeader />
          <div className="center books-list">
            {booksList.map((book, index) => (
              <div className="column book-row stud-book-row">
                <IndividualBook
                  key={index}
                  name={book.name}
                  author={book.author}
                />
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default Student;
