import React, { useState } from "react";
import Book from "./Book";
import NavBar from "./NavBar";
import LibraryListHeader from "./LibraryListHeader";
import SearchBar from "./SearchBar";

let books = require("../static/books");

const Librarian = () => {
  const [pass, setPass] = useState("");
  const [booksList, setBooksList] = useState([...books]);

  const passUpdate = (e) => setPass(e.target.value);

  const passCheck = () => {
    if (pass === "dsce") setPass("done");
    else {
      alert("wrong password");
      setPass("");
    }
  };

  const booksListUpdate = (e) => {
    let list = [];
    list = books.filter((book) =>
      book.name.toLowerCase().includes(e.target.value.toLowerCase())
    );
    setBooksList([...list]);
  };

  const bookDelete = (name) => {
    books = books.filter((book) => book.name !== name);
    setBooksList([...books]);
  };

  const bookAccept = (id, usn, name) => {
    if (isNaN(id)) return;
    for (let i = 0; i < books.length; ++i) {
      if (books[i].name === name) {
        books[i].id.push(id);
        books[i].quantity += 1;
        books[i].issued = books[i].issued.filter((stud) => stud.usn !== usn);
        setBooksList([...books]);
        return;
      }
    }
  };

  const bookIssue = ({ bookId, name, branch, usn, year }, bookName) => {
    console.log(typeof bookId);
    console.log(isNaN(parseInt(year)));
    if (isNaN(parseInt(bookId.value)) || isNaN(parseInt(year.value))) return;
    console.log("here");
    for (let i = 0; i < books.length; i++) {
      if (books[i].name === bookName) {
        books[i].issued.push({
          name: name.value,
          usn: usn.value,
          branch: branch.value,
          year: parseInt(year.value),
        });
        books[i].quantity -= 1;
        books[i].id = books[i].id.filter(
          (each) => each !== parseInt(bookId.value)
        );
        setBooksList([...books]);
        return;
      }
    }
  };

  return (
    <>
      <NavBar />
      {pass !== "done" ? (
        <div className="mt-5 center">
          <h3 className="mb-4">You are signing in as Librarian</h3>
          <div className="my-3 mx-auto column librarian-box">
            <input
              className="librarian-pass-box"
              type="password"
              value={pass}
              required
              onChange={passUpdate}
            />
            <span className="placeholder">Password</span>
            <button className="btn btn-lg btn-outline-dark" onClick={passCheck}>
              Login
            </button>
          </div>
        </div>
      ) : (
        <div className="row library">
          <SearchBar onChange={booksListUpdate} />
          <br />
          <div className="my-5 center">
            <LibraryListHeader />
            <div className="center books-list">
              {booksList.map((book, index) => (
                <Book
                  index={index}
                  {...book}
                  onBookAccept={bookAccept}
                  onDelete={bookDelete}
                  onBookIssue={bookIssue}
                />
              ))}
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Librarian;
